f;AA3/E8B;EACK,sBC/DxB;AD4jFX;AA1/E8B;EACK,yBCpErB;ADgkFd;AAz/E8B;EACK,yBCtEpB;ADikFf;AAx/E8B;EACK,yBCxEvB;ADkkFZ;AAn/E8B;EACK,yBC5EnB;ADikFhB;AAj/EmC;EACK,aAAA;AAm/ExC;AA/+E8B;EACK,gBAAA;EACA,kBAAA;AAi/EnC","sourcesContent":[".editor-styles-wrapper {\n     @import 'scss/global.scss';\n     padding-left: 2rem !important;\n     padding-right: 2rem !important;\n\n     .editor-post-title {\n          text-transform: none;\n     }\n\n     .block-editor-block-list__block {\n          margin: 0 auto;\n          padding: 1rem 0;\n     }\n\n     .acf-field[data-name=\"colour_picker\"] {\n          ul.acf-radio-list {\n               display: flex;\n               flex-wrap: wrap;\n               gap: 1rem;\n               align-items: flex-start;\n               overflow: visible;\n\n               &:focus-within {\n                    border-color: transparent;\n               }\n\n               li {\n                    width: fit-content;\n                    margin: 0 8px 8px 0;\n                    border-radius: 4px;\n                    display: flex;\n\n                    &:nth-child(3n) {\n                         margin-right: 0;\n                    }\n\n                    label {\n                         display: flex;\n                         flex-direction: column;\n                         width: 100%;\n                         justify-content: center;\n                         align-items: center;\n                         text-align: center;\n                         line-height: 1.2;\n                         font-size: 10px;\n                         text-transform: uppercase;\n                         font-weight: 700;\n                         border-radius: 4px;\n\n                         &.selected {\n                              input {\n                                   outline: 2px solid #2271b1;\n                                   outline-offset: 3px;\n                              }\n                         }\n\n                         input[type=\"radio\"] {\n                              width: 3rem;\n                              height: 36px;\n                              border-radius: 4px;\n                              margin: 0;\n                              margin-bottom: 0.5rem;\n\n                              &[value=\"red\"] {\n                                   background-color: $pf-raspberry;\n                              }\n\n                              &[value=\"dark-red\"] {\n                                   background-color: $pf-raspberry;\n                              }\n\n                              &[value=\"light-red\"] {\n                                   background-color: $pf-raspberry;\n                              }\n\n                              &[value=\"off-white\"] {\n                                   background-color: $pf-off-white;\n                              }\n\n                              &[value=\"white\"] {\n                                   background-color: $pf-white;\n                              }\n\n                              &[value=\"charcoal\"] {\n                                   background-color: $pf-charcoal;\n                              }\n\n                              &[value=\"raspberry\"] {\n                                   background-color: $pf-raspberry;\n                              }\n\n                              &[value=\"yellow\"] {\n                                   background-color: $pf-yellow;\n                              }\n\n                              // &[value=\"light-green\"] {\n                              //      background-color: $pf-light-green;\n                              // }\n\n                              &[value=\"dark-green\"] {\n                                   background-color: $pf-dark-green;\n                              }\n\n                              &:checked {\n                                   &::before {\n                                        display: none;\n                                   }\n                              }\n\n                              &:focus {\n                                   box-shadow: none;\n                                   border-color: none;\n                              }\n                         }\n                    }\n               }\n          }\n     }\n}","$pf-base-size: 1rem;\n\n/**********************\n Container sizes\n***********************/\n$col--sm: 540px; // (max-width: 540px)\n$col--md: 720px; // (max-width: 720px)\n$col--lg: 960px; // (max-width: 960px)\n$col--xl: 1140px; // (max-width: 1140px)\n\n/**********************\n Colours\n***********************/\n\n// Brand colours\n$pf-off-white: #f8faf8;\n$pf-charcoal: #262626;\n$pf-white: #fff;\n$pf-raspberry: #993050;\n$pf-secondary-raspberry: #882a47;\n$pf-yellow: #FAB200;\n$pf-yellow--alt: #E0A000;\n$pf-shadow: #464B44;\n$pf-light-green: #60CE71;\n$pf-dark-green: #2D6E51;\n$pf-grey: #D9D9D9;\n\n\n\n// Text colours\n$body-text-color: $pf-charcoal;\n\n/**********************\n Breakpoints\n***********************/\n\n$breakpoints: (\n  sm: (breakpoint: 576px,\n    max-width: 540px,\n  ),\n  md: (breakpoint: 768px,\n    max-width: 720px,\n  ),\n  lg: (breakpoint: 992px,\n    max-width: 960px,\n  ),\n  xl: (breakpoint: 1200px,\n    max-width: 1140px,\n  ),\n  xxl: (breakpoint: 1650px,\n  ),\n);\n\n$size--xs: $pf-base-size * 0.6875;\n//11px\n$size--sm: $pf-base-size * 0.8125;\n//13px\n$size--base: $pf-base-size;\n//16px\n$size--md: $pf-base-size * 1.125;\n//18px\n$size--lg: $pf-base-size * 1.5;\n//24px\n$size--xl: $pf-base-size * 1.875;\n// 30px\n$size--xxl: $pf-base-size * 3.125;\n//50px\n$size--giant: $pf-base-size * 4.6875;\n\n\n/**********************\n Spacing\n***********************/\n\n$spacing--xs: $pf-base-size * 1;\n$spacing--sm: $pf-base-size * 1.5;\n$spacing--md: $pf-base-size * 2;\n$spacing--lg: $pf-base-size * 3;\n$spacing--xl: $pf-base-size * 4;\n\n/**********************\nFonts\n***********************/\n\n$font--primary: 'Jost', 'Avenir', 'Futura', 'Century Gothic', Helvetica, Arial, sans-serif;\n$font--secondary: 'Katibeh', 'Unna', 'Big Caslon', Georgia, serif;\n$font--tertiary: 'Montserrat', Helvetica, Arial, sans-serif;\n\n\n/**********************\nHeader\n***********************/\n$header-height-mobile: 78px;\n$header-height-desktop: 60px;","// Modern CSS reset: https://github.com/hankchizljaw/modern-css-reset\n\n/* Box sizing rules */\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\n\n// /* Remove default padding */\n// ul[class],\n// ol[class] {\n//     padding: 0;\n// }\n\n/* Remove default margin */\nbody,\nh1,\nh2,\nh3,\nh4,\np,\nul,\nol,\nfigure,\nblockquote,\ndl,\ndd {\n    margin: 0;\n}\n\n/* Set core root defaults */\nhtml {\n    scroll-behavior: smooth;\n}\n\n/* Set core body defaults */\nbody {\n    min-height: 100vh;\n    text-rendering: optimizeSpeed;\n    line-height: 1.5;\n}\n\n/* Remove list styles on ul, ol elements with a class attribute */\nul[class],\nol[class] {\n    list-style: none;\n}\n\n/* A elements that don't have a class get default styles */\na:not([class]) {\n    text-decoration-skip-ink: auto;\n}\n\n/* Make images easier to work with */\nimg,\npicture {\n    max-width: 100%;\n    display: block;\n}\n\n/* Natural flow and rhythm in articles by default */\narticle>*+* {\n    margin-top: 1em;\n}\n\n/* Inherit fonts for inputs and buttons */\ninput,\nbutton,\ntextarea,\nselect {\n    font: inherit;\n}\n\n/* Blur images when they have no alt attribute */\nimg:not([alt]) {\n    filter: blur(10px);\n}\n\n/* Remove all animations and transitions for people that prefer not to see them */\n@media (prefers-reduced-motion: reduce) {\n    * {\n        animation-duration: 0.01ms !important;\n        animation-iteration-count: 1 !important;\n        transition-duration: 0.01ms !important;\n        scroll-behavior: auto !important;\n    }\n}","/**\nThe function `map-deep-get` is a custom function written in a Sass/SCSS stylesheet. Its purpose is to retrieve a nested value from a map (also known as a dictionary or associative array) using a variable number of keys.\n\nHere's a breakdown of how the function works:\n\n1. The function is defined using the `@function` directive and named `map-deep-get`. It takes two parameters:\n   - `$map`: The map from which the value will be retrieved.\n   - `$keys...`: A variable number of keys that specify the path to the desired value in the map.\n\n2. Inside the function, there is a `@each` loop that iterates over each `$key` provided in `$keys...`.\n\n3. Within the loop, the `map-get()` function is used to retrieve the value associated with the current `$key` from the `$map`. The retrieved value is then assigned back to the `$map` variable, effectively updating it to the nested map.\n\n4. After the loop finishes, the final value of `$map` is returned using the `@return` directive.\n\nIn summary, the `map-deep-get` function allows you to access a nested value within a map by providing a sequence of keys that define the path to the desired value. Each key is used to access a nested map until the final value is reached, which is then returned.\n*/\n\n@function map-deep-get($map, $keys...) {\n\t@each $key in $keys {\n\t\t$map: map-get($map, $key);\n\t}\n\t@return $map;\n}","/**\n * Background Colours\n * -----------------------------------------------------------------------------\n */\n\n.bg--red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--dark-red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--light-red {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--off-white {\n    background-color: $pf-off-white;\n    color: $pf-charcoal;\n}\n\n.bg--white {\n    background-color: $pf-white;\n    color: $pf-charcoal;\n}\n\n.bg--charcoal {\n    background-color: $pf-charcoal;\n    color: $pf-white;\n}\n\n.bg--yellow {\n    background-color: $pf-yellow;\n    color: $pf-charcoal;\n}\n\n.bg--yellow-alt {\n    background: $pf-yellow--alt;\n    color: $pf-charcoal;\n}\n\n.bg--raspberry {\n    background-color: $pf-raspberry;\n    color: $pf-white;\n}\n\n.bg--dark-green {\n    background-color: $pf-dark-green;\n    color: $pf-white;\n}\n\n/**\n * Text Colours\n * -----------------------------------------------------------------------------\n */\n.red {\n    color: $pf-raspberry;\n}\n\n.dark-red {\n    color: $pf-raspberry;\n}\n\n.light-red {\n    color: $pf-raspberry;\n}\n\n.off-white {\n    color: $pf-off-white;\n}\n\n.white {\n    color: $pf-white;\n}\n\n.charcoal {\n    color: $pf-charcoal;\n}","// Primary font - Jost (weights 0 - 800)\n@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');\n\n// Secondary font - Katibeh\n@import url('https://fonts.googleapis.com/css2?family=Katibeh&display=swap');\n\np,\nli,\na {\n  font-family: $font--primary;\n  font-size: $size--md;\n  font-weight: 300;\n  line-height: $spacing--md;\n}\n\n.text__standfirst {\n  li, p, a {\n    font-family: $font--primary;\n    font-size: $size--lg;\n    font-weight: 400;\n    line-height: $spacing--md;\n    padding-bottom: 1.5rem;\n  }\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-family: $font--primary;\n  text-transform: uppercase;\n  font-weight: 700;\n}\n\n.h1,\n.h2,\n.h3,\n.h4,\n.h5,\n.h6 {\n  font-family: $font--primary;\n}\n\nh2 {\n  letter-spacing: 2px;\n}\n\n.giant-h1 {\n\n  font-family: $font--primary;\n  font-weight: 600;\n  font-size: $size--xxl;\n  line-height: 1.2;\n\n  @include media-breakpoint('lg') {\n    font-size: $size--giant;\n    line-height: 1.2;\n  }\n}\n\n.h1,\nh1:not([class]),\n.heading__h1 {\n  font-size: $size--xxl;\n}\n\n.h2,\nh2:not([class]),\n.heading__h2 {\n  font-size: $size--xl;\n}\n\n.h3,\nh3:not([class]),\n.heading__h3 {\n  font-size: $size--lg;\n}\n\n.h4,\nh4:not([class]),\n.heading__h4 {\n  font-size: $size--md