ercase;\n        font-weight: 700;\n        letter-spacing: 1.2px;\n      }\n    }\n  }\n\n  .button {\n    margin: 0 auto;\n  }\n\n  &__players {\n    h2 {\n      text-align: center;\n    }\n\n    &__header {\n      text-transform: uppercase;\n      background-color: $pf-off-white;\n      width: fit-content;\n      padding: 1rem 1rem 1rem 6rem;\n      border-radius: 0 4px 4px 0;\n      box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);\n      position: relative;\n      margin-bottom: $spacing--md;      \n\n      &::before {\n        content: '';\n        position: absolute;\n        top: 0;\n        left: 0;\n        width: 80px;\n        height: 100%;\n        background-image: url('../../../images/icons/colour-chevron.svg');\n        background-size: cover;\n      }\n\n      .chevron {\n        position: relative;\n\n        svg {\n          position: absolute;\n          left: 0;\n        }\n\n        svg:nth-of-type(1) {\n          z-index: 1;\n        }\n\n        svg:nth-of-type(2) {\n          left: 1rem;\n        }\n      }\n    }\n\n    &__content {\n      display: flex;\n      flex-direction: column;\n      justify-content: center;\n      gap: $spacing--xl;\n\n      .football-team__players__position {\n        position: relative;\n      }\n\n      .football-team__players__header {\n        align-self: flex-start;\n        position: -webkit-sticky; /* For Safari */\n        position: sticky;\n        top: ($header-height-mobile + 40px);\n        z-index: 1;\n        transition: left 0.3s ease;\n\n        @include media-breakpoint('lg') {\n          top: ($header-height-desktop + 40px);\n        }\n      }\n    }\n\n    &__list {\n      display: grid;\n      gap: 2rem;\n\n      @include media-breakpoint('sm') {\n        display: grid;\n        grid-template-columns: repeat(2, 1fr);\n        row-gap: 4rem;\n        \n      }\n\n        @include media-breakpoint('lg') {\n            grid-template-columns: repeat(3, 1fr);\n            column-gap: 2rem;\n        }